'use client'

import { useState } from 'react'
import { submitCareerForm } from '@/lib/submitCareerForm'

export default function CareerForm() {
    const [form, setForm] = useState({
        fullName: '',
        email: '',
        phone: '',
        position: '',
        coverLetter: '',
        resume: null as File | null,
    })

    const [submitting, setSubmitting] = useState(false)
    const [success, setSuccess] = useState(false)
    const [error, setError] = useState<string | null>(null)

    const handleChange = (e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>) => {
        const { name, value } = e.target
        setForm(prev => ({ ...prev, [name]: value }))
    }

    const handleFileChange = (e: React.ChangeEvent<HTMLInputElement>) => {
        if (e.target.files && e.target.files[0]) {
            setForm(prev => ({ ...prev, resume: e.target.files![0] }))
        }
    }

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault()
        setSubmitting(true)
        setError(null)

        try {
            await submitCareerForm(form)
            setSuccess(true)
            setForm({
                fullName: '',
                email: '',
                phone: '',
                position: '',
                coverLetter: '',
                resume: null,
            })
        } catch (err) {
            console.error(err)
            setError('Something went wrong. Please try again.')
        } finally {
            setSubmitting(false)
        }
    }

    return (
        <form onSubmit={handleSubmit} className="max-w-xl mx-auto space-y-6 bg-gray-100 p-8 rounded shadow">
            <h2 className="text-2xl font-semibold">Apply for this Position</h2>

            {success && <p className="text-green-600 font-medium">Your application has been submitted!</p>}
            {error && <p className="text-red-600 font-medium">{error}</p>}

            <div>
                <label className="block font-medium">Full Name</label>
                <input type="text" name="fullName" value={form.fullName} onChange={handleChange} required className="w-full mt-1 p-2 border" />
            </div>

            <div>
                <label className="block font-medium">Email</label>
                <input type="email" name="email" value={form.email} onChange={handleChange} required className="w-full mt-1 p-2 border" />
            </div>

            <div>
                <label className="block font-medium">Phone</label>
                <input type="text" name="phone" value={form.phone} onChange={handleChange} className="w-full mt-1 p-2 border" />
            </div>

            <div>
                <label className="block font-medium">Position</label>
                <input type="text" name="position" value={form.position} onChange={handleChange} className="w-full mt-1 p-2 border" />
            </div>

            <div>
                <label className="block font-medium">Cover Letter</label>
                <textarea name="coverLetter" value={form.coverLetter} onChange={handleChange} rows={4} className="w-full mt-1 p-2 border" />
            </div>

            <div>
                <label className="block font-medium">Resume (PDF, DOCX)</label>
                <input type="file" accept=".pdf,.doc,.docx" onChange={handleFileChange} className="w-full mt-1" />
            </div>

            <button
                type="submit"
                disabled={submitting}
                className="bg-blue-600 text-white py-2 px-6 rounded hover:bg-blue-700 disabled:opacity-50"
            >
                {submitting ? 'Submitting...' : 'Submit Application'}
            </button>
        </form>
    )
}
